*{
  padding: 0;
  margin: 0;
  box-shadow: none;
}

.body{
  height: 100vh;
  background-image: url("./components/images/background.jpg");
  background-size: cover;
}
.box{
  position: relative;
  width: 55%;
}
.para{
  position: absolute;
  top: 270px;
  left: 300px;
  font-size: 1rem;
  color: white;
}

h1{
  position: absolute;
  top: 300px;
  left: 300px;
  color: white;
  font-weight: bold;
  font-size: 3rem;
}

.para-2{
  position: absolute;
  top: 380px;
  left: 300px;
  color: white;
}

img{
  position: absolute;
  top: 80px;
  right: 50px;
  height: 600px;
  width: 600px;
}

input{
  position: absolute;
  top: 500px;
  left: 330px;
  height: 40px;
  opacity: 0.5;
  border-radius: 10px;
}
